:root {
  --box-shadow: 0px 1px 6px rgba(46, 47, 66, .08), 0px 1px 1px rgba(46, 47, 66, .16), 0px 2px 1px rgba(46, 47, 66, .08);
}

.loader {
  width: 100px;
  height: 100px;
  margin: auto;
  position: relative;
}

.loader:before, .loader:after {
  content: "";
  border-radius: 50%;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  box-shadow: inset 0 0 10px 2px rgba(0, 0, 0, .3);
}

.loader:after {
  animation: rotate 2s linear infinite;
  box-shadow: inset 0 2px #ff3d00;
}

@keyframes rotate {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

.cat-info {
  width: 500px;
  box-shadow: var(--box-shadow);
  border-radius: 2px;
  margin-top: 40px;
  margin-left: auto;
  margin-right: auto;
  padding: 15px;
  overflow: hidden;
}

.card-wrap {
  padding: 40px;
}

.cat-desc {
  margin-top: 20px;
}

.cat-temp {
  margin-top: 20px;
  font-size: 14px;
}

.is-hidden {
  display: none;
}

